.dialogWrapper {
    position: fixed;
    background: hsla(0, 0%, 0%, 0.4);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
    #leftSide {
       width: 250px;
       min-height: 400px;
       background: url('./img/001.jpg') no-repeat, url('./img/002.jpg') no-repeat;
       background-size: cover;
       position: relative;
       overflow: hidden; 
       .leftSideImg {
           margin: 0px;
           height: 100%;
       }
        &::before {
            content: '';
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(135deg, rgba(144, 185, 208, .5),
             rgba(165, 145, 207, .5));
            filter: blur(6px) saturate(1.5) contrast(2);
            margin: -30px;
        }     
    }
    #rightSide {
        background: #2C3868;
        padding: 20px 0;
    }   
    .dialog {
        color: #FFF;
        display: flex;
        box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
        header {
            padding: 20px;
            display: flex;
            justify-content: space-between;
        }
        main {
            padding: 20px;
            padding-top: 0;
        }
    }
}


