#resumePreview {
    color: dodgerblue;
    background: #FFF;
    box-shadow: 0 0px 4px 1px rgba(0, 0, 0, 0.5);
    padding: 2em;
    color: #333;
    line-height: 1.2;
    overflow: auto;
    * {
        box-sizing: border-box;
        font-variant: normal;
        font-weight: normal;
    }
    img {
        border: 5px solid #74BBFF;
        box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.5);
    }
    section + section {
        margin-top: 2em;
    }
    p {
        white-space: pre-line;
    }
    
    section {
        background: #EEE;
        border-left: 6px solid;
        box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.5);
        padding: 15px;
        &:hover {
            box-shadow: 0 2px 10px 0px rgba(0, 0, 0, 0.5);
        }
        h2:first-child {
            display: inline-block;
            padding: 0.2em;
            margin-bottom: 0.5em;
        }
    }
    section[data-name="profile"] {
        position: relative;
        padding: 20px;
        h1 {
            margin: 0.1em 0;
            font-size: 4em;
        }
        h2,h3 {
            margin: 0.3em 0;
        }
        .resumeAvatar {
        width: 133px;
        height: 200px;
        position: absolute;
        right: 200px;
        overflow: hidden;
            img {
               /* width: 133px;
                height: 200px;*/
                border: none;
                margin: 0;
            }
        }
    }
    section[data-name="workHistory"],
    section[data-name="projects"],
    section[data-name="awards"] {
        li + li {
            margin-top: 1em;
        }
        li {
            h3 {
                border-bottom: 1px solid #999;
                padding-bottom: .3em;
                margin-bottom: .3em;
            }
        }
    }
    section[data-name="education"] {
        li {
            line-height: 1.5;
            h3 {
                display: inline-block;
            }
        }
    }
    section[data-name="contacts"] {
        td:first-child {
            padding-right: 1em;
        }
    }
}


